import React, { Component } from 'react'
import axios from 'axios';

export default class KitchenDetail extends Component {

    constructor(props) {
        super(props);
        this.state = {
          orders: []
        };
        this.checkDate = this.checkDate.bind(this);
      }

    componentDidMount() {
        axios
          .get("http://localhost:3000/orders")
          .then(response => {
              console.log(response.data[0])
            this.setState({
              orders: response.data
            });
          })
          .catch(err => {
            console.log(err);
          });
      }

    checkDate(){
        console.log("checkDate function is called")
        let thisDay = Date.parse(new Date(parseInt(this.params)))
        console.log("thisDay: " + thisDay)
        let newDate = new Date(this.state.orders.date);
        let dateYear = newDate.getFullYear();
        let dateMonth = newDate.getMonth()+1;
        let dateDay = newDate.getDate();
        let totalDate = `${dateDay}/${dateMonth}/${dateYear}`
        for(let i = 0; i < this.state.orders.length; i++){
            if((thisDay.getDate() + i) === newDate.getDate() && thisDay.getMonth() === newDate.getMonth() && thisDay.getFullYear() === newDate.getFullYear()){
                totalDate = `${i} days ago`
                console.log("totalDate: " + totalDate)
                return totalDate
            }else{
                console.log("no match")
            }
        }
    }

    getThisDate(id) {
        let found = this.state.orders.find(order => {
            let foundOrderFormat = (Date.parse(order.date))
            console.log(foundOrderFormat)
            let newDate = new Date(parseInt(id))

            let idFormat = Date.parse(newDate)
            console.log("idFormat: " + idFormat)
          return foundOrderFormat === idFormat;
        });
        return found;
      };



    render() {
        // const dateFormat = new Date(this.params).getDate() + "/" + new Date(this.params).getMonth() + "/" + new Date(this.params).getFullYear()
        // console.log(dateFormat)

        const { params } = this.props.match;
        const foundOrders = this.getThisDate(params.id);
        console.log("foundOrders " + foundOrders)

        return (
            <div className="kitchenDetailDiv">
                <h4>Name</h4>
                <p>{params.id}</p>
                <p>info over de bestelling: bla bla bla</p>
                <p>notes: bla bla bla</p>
                <button onClick={this.checkDate}>check</button>
            </div>
        )
    }
}
